<template>
<el-container  :style="backgroundStyle">
    <el-header>2025</el-header>
  <div class="timeline">
    <el-page-header @back="goBack" content="历程">
    </el-page-header>
    <div v-for="(activity, index) in activities" :key="index" class="timeline-item">
      <div class="timeline-item-content" :style="{ backgroundImage: `url(${activity.cover})` }">
        <h3>{{ activity.content }}</h3>
        <p>{{ activity.timestamp }}</p>
        <button @click="togglePlay(activity.audio, index)">
          {{ isPlaying && currentTrack === index ? '暂停' : '播放' }}
        </button>
        <div v-if="currentTrack === index" class="controls">
          <input type="range" min="0" max="100" v-model="progress" @input="seek" />
          <input type="range" min="0" max="1" step="0.01" v-model="volume" @input="setVolume" />
        </div>
      </div>
    </div>
    <audio ref="audioPlayer" @timeupdate="updateProgress" @ended="resetPlayer"></audio>
  </div>
</el-container>
</template>

<script>
export default {
  data() {
    return {
      backgroundImage: require('@/assets/images/bg.jpg'),
      activities: [
        {
          content: '未体験HORIZON',
          timestamp: '2019-9-25',
          cover: require('@/assets/images/4单.jpg') ,
          audio: require('@/assets/images/4单.mp3')
        },
        {
          content: 'HAPPY PARTY TRAIN',
          timestamp: '2017-04-05',
          cover: require('@/assets/images/3单.jpg'),
          audio: require('@/assets/images/3单.mp3')
        },
        {
          content: '恋になりたいAQUARIUM',
          timestamp: '2016-04-27',
          cover: require('@/assets/images/2单.jpg' ),
          audio: require('@/assets/images/2单.mp3')
        },
        {
          content: '君のこころは輝いてるかい？',
          timestamp: '2015-10-07',
          cover: require('@/assets/images/1单.jpg'),
          audio: require('@/assets/images/1单.mp3')
        }
      ],
    isPlaying: false,
      currentTrack: null,
      progress: 0,
      volume: 1
    };
  },
  computed: {
    backgroundStyle() {
      return {
        backgroundImage: `url(${this.backgroundImage})`
      };
    }
  },
  methods: {
    togglePlay(audioSrc, index) {
      const audioPlayer = this.$refs.audioPlayer;
      if (this.isPlaying && this.currentTrack === index) {
        audioPlayer.pause();
        this.isPlaying = false;
      } else {
        if (this.currentTrack !== index) {
          audioPlayer.src = audioSrc;
          this.progress = 0;
        }
        audioPlayer.play();
        this.isPlaying = true;
        this.currentTrack = index;
      }
    },
    updateProgress() {
      const audioPlayer = this.$refs.audioPlayer;
      this.progress = (audioPlayer.currentTime / audioPlayer.duration) * 100;
    },
    seek(event) {
      const audioPlayer = this.$refs.audioPlayer;
      const seekTime = (event.target.value / 100) * audioPlayer.duration;
      audioPlayer.currentTime = seekTime;
    },
    setVolume(event) {
      const audioPlayer = this.$refs.audioPlayer;
      audioPlayer.volume = event.target.value;
    },
    resetPlayer() {
      this.isPlaying = false;
      this.currentTrack = null;
      this.progress = 0;
    },
    goBack(){
      this.$router.go(-1);
    }
  }
};
</script>

<style>
.timeline {
  position: relative;
  /* margin: 50px 0; */
  padding: 0;
  list-style: none;
}

.timeline::before {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  width: 4px;
  background: #ddd;
  left: 50%;
  transform: translateX(-50%);
}

.timeline-item {
  position: relative;
  margin: 40px 0; /* 增加节点之间的距离 */
  padding-left: 50%;
  text-align: left;
}

.timeline-item:nth-child(odd) {
  padding-left: 0;
  padding-right: 50%;
  text-align: right;
}

.timeline-item::before {
  content: '';
  position: absolute;
  top: 20px;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background: #fff;
  border: 4px solid #007bff;
  left: 50%;
  transform: translateX(-50%);
  z-index: 1;
}

.timeline-item:nth-child(odd)::before {
  left: auto;
  right: 50%;
  transform: translateX(50%);
}

.timeline-item-content {
  padding: 20px;
  background: #f9f9f9;
  background-size: cover;
  background-position: center;
  position: relative;
  border-radius: 6px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  width: calc(45% - 40px); 
  color: white; 
  
}
.timeline-item-content h3, .timeline-item-content p {
  background: rgba(0, 0, 0, 0.5);
  padding: 5px;
  border-radius: 4px;
}

.timeline-item:nth-child(odd) .timeline-item-content {
  left: 50%;
  text-align: right; 
}

.timeline-item:nth-child(even) .timeline-item-content {
  left: 5%;
  text-align: left; 
}

.timeline-item-content button {
  margin-top: 10px;
  padding: 10px 20px;
  background-color: orange;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}
.timeline-item-content button:hover {
  background-color: #0056b3;
}
.controls {
  margin-top: 10px;
}
.controls input[type="range"] {
  width: 100%;
  margin:5px 0;
}
.el-header {
  color: orange;
  text-align: center;
  font-size: 40px;
}
</style>






